<template>
  <div>
    <h1>忘记密码</h1>
    <van-form @submit="onSubmit">
      <van-field v-model="ruleForm.username" name="username" label="用户名" placeholder="用户名" />
      <van-field v-model="ruleForm.phone" type="text" name="phone" label="手机号码" placeholder="原密码" />
      <van-field v-model="ruleForm.yan" center clearable label="短信验证码" placeholder="请输入短信验证码(200)">
        <template #button>
          <van-button
            @click="sendcode"
            size="small"
            type="primary"
          >{{num > 0 ? '倒计时'+num+'秒' : '发送验证码'}}</van-button>
        </template>
      </van-field>
      <van-field
        v-model="ruleForm.newPass"
        name="newPass"
        type="password"
        label="新密码"
        placeholder="新密码"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import Vue from "vue";
import { Form, Toast } from "vant";
Vue.use(Form);
Vue.use(Toast);

export default {
  name: "repassword",
  data() {
    return {
      btnval: "获取验证码",
      num: -1,
      ruleForm: {
        username: "",
        // oldpass: "",
        newPass: "",
        yan: "",
        phone: "",
      },
    };
  },
  methods: {
    sendcode(ev) {
      console.log(123);
      console.log(ev.target);
      //發送請求
      // if (this.ruleForm.phone != "") {
      //   this.$refs["ruleForm"].validate(async () => {
      ev.target.disabled = true; //點擊第一次就發送請求
      this.num = 60;
      let times = 0;
      let timer = setInterval(async () => {
        let num = this.num;
        this.num = --num;
        console.log(this.num, 899); //获取验证码的代码
        const { username } = this.ruleForm;
        const { phone } = this.ruleForm;

        const { data } = await this.$request.get(
          `/phonecheck/check/?phone=${phone}&username=${username}`
        ); //获取验证码存到数据库
        //返回的数据是发送成功
        console.log("返回的数据", data);
        times++;
        if (times == 60) {
          ev.target.disabled = false; //點擊第一次就發送請求
          this.num = -1;
          clearInterval(timer);
        }
      }, 1000);
      // });
      // }else{
      //   alert('请输入手机号码')
      // }
    },
    async onSubmit(values) {
      console.log("参数", values);
      const { yan } = this.ruleForm;
      const { username } = this.ruleForm;
      const { newPass } = this.ruleForm;
      if (username !== "") {
        //点击发送请求，获得数据库里存的验证码，如果现在的这个和数据库的里的一样
        //则再发起后面的请求==》修改密码
        const { data } = await this.$request.put("/phonevcode/", {
          yan,
          username,
          newPass,
        });
        console.log("请求修改密码返回来的数据", data);
        if (data.code === 1) {
          Toast.success("恭喜你修改成功!");
          this.$router.push("/login");
        } else if (data.code === 10) {
          Toast.fail("验证码不正确!");
        } else {
          Toast.fail("修改失败!");
        }
      } else {
        Toast.fail("请输入用户名!");
      }
    },
  },
};
</script>